  margin-top: 30px auto;
  /* right: 0px; */
}
.home_container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  position: relative;
  top: 3rem;
  width: 90vw;
  margin-bottom: 170px;
  /* background-color: lightgrey; */
}

.home_container .home_head_wrapper {
  position: relative;
  top: 2rem;
  display: flex;
  flex-direction: column;
  width: 40vw;
}
.home_container .home_head_wrapper h1 {
  font-size: 4.5rem;
  font-family: "Montserrat";
}
.home_container .home_head_wrapper h1 span {
  color: #1e6fd9;
}
.home_container .home_head_wrapper p {
  /* position: relative; */
  top: -4rem;
  font-size: 2rem;
  opacity: 0.4;
}
.home_container .image_container img {
  position: relative;
  height: 26rem;
  width: 25rem;
  top: 4rem;
  border-style: solid;
  border-width: 5px;
  border-top: 0px;
  border-left: 0px;
  border-color: #1e6fd9;
  margin-right: 120px;
  object-fit: cover;
  object-position: 15% 100%;
  transform: rotate(90deg);
}
/* LANDING PAGE SCROLL ICONE*/

.Landing-container {
  position: absolute;
  width: 50px;
  height: 50px;
  margin-left: 550px;
  margin-top: 400px;
  /* background-color: yellowgreen; */
}
.chevron {
  /* position: absolute; */
  width: 28px;
  height: 8px;
  opacity: 0;
  transform: scale3d(0.5, 0.5, 0.5);
  animation: move 3s ease-out infinite;
}

.chevron:first-child {
  animation: move 3s ease-out 1s infinite;
}

.chevron:nth-child(2) {
  animation: move 3s ease-out 2s infinite;
}

.chevron:before,
.chevron:after {
  content: " ";
  position: absolute;
  top: 0;
  height: 100%;
  width: 51%;
  background: #fff;
  margin-top: 0px;
}

.chevron:before {
  left: 0;
  transform: skew(0deg, 30deg);
}

.chevron:after {
  right: 0;
  width: 50%;
  transform: skew(0deg, -30deg);
}

@keyframes move {
  25% {
    opacity: 1;
  }
  33% {
    opacity: 1;
    transform: translateY(30px);
  }
  67% {
    opacity: 1;
    transform: translateY(40px);
  }
  100% {
    opacity: 0;
    transform: translateY(55px) scale3d(0.5, 0.5, 0.5);
  }
}

.text {
  display: block;
  margin-top: 75px;
  margin-left: -30px;
  font-family: "Helvetica Neue", "Helvetica", Arial, sans-serif;
  font-size: 12px;
  color: #fff;
  text-transform: uppercase;
  white-space: nowrap;
  opacity: 0.25;
  animation: pulse 2s linear alternate infinite;
}

@keyframes pulse {
  to {
    opacity: 1;
  }
}

/* Work */
.work_container {
  background-color: #66669a42;
